import React from 'react'
import Button from '../../../../../../../../components/button/Button'

import styles from './styles/_hire-me.module.scss'

function HireMe () {
	function openGmail (gmailLink: string, delay: number) {
		setTimeout(() => {
			window.open(gmailLink, '_self')
		}, delay)
	}

	return (
		<Button text='Hire me' className={styles['hire-me']} userFunction={() => openGmail('mailto:juliapirogova03@gmail.com', 1000)} />
	)
}

export default HireMe
